import React from 'react';
import MainContainer from "./containers/MainContainer/mainContainer.js"
import MatchesPage from "./containers/MatchesContainer/matchesContainer.js"
import SignUpPage from "./containers/SignUpContainer/SignUpContainer.js"
import LoginPanel from "./components/loginPanel/loginPanel.js"
import CreateAccountPanel from "./components/createAccountPanel/createAccountPanel.jsx"
import CreateProfilePanel from "./components/createProfilePanel/createProfilePanel.js"
import SearchPanel from "./components/searchPanel/searchPanel.js"

import API from "./util/API.js";



import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { changeCredentials, changeProfile, changeProfileErrors, changeNewAccount, changeNewAccountErrors, changeProfileWasCompleted, changeSearchParams, changeUserIsLoggedIn, changeAccountWasCompletedId } from "./actions/index.js"

import './App.css';

// handleLoginClick();

let mapStateToProps = (state) => {
    return {
        // profile: state.profile,
        // newAccount: state.newAccount,
        // profileErrors: state.profileErrors,
        // newAccountErrors: state.newAccountErrors,
        accountWasCompleted: state.accountWasCompleted,
        // accountWasCreated: state.accountWasCreated,
        // searchParams: state.searchParams,
        userIsLoggedIn: state.userIsLoggedIn
    };
}

let matchDispatchtoProps = (dispatch) => {
    return bindActionCreators({ changeCredentials, changeProfile, changeProfileErrors, changeNewAccount, changeNewAccountErrors, changeProfileWasCompleted, changeSearchParams, changeUserIsLoggedIn, changeAccountWasCompletedId }, dispatch)
}


class App extends React.Component{

    handleLoginClick = (e) => {
        e.preventDefault();
        
        API.validateLogin(this.props.credentials)
            .then((res) => {
                if (res.success) {
                    console.log(res.id);
                    console.log(res.success);

                    // this.props.changeUserIsLoggedIn(res.id, res.success)
                    // this.props.changeAccountWasCreated(true)
                }
            });
    }

    handleLoginInputChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;

        this.props.changeCredentials(name, value)
    } 


    componentToRender = (bool, obj) => {
        let toRender;

        if(!bool){
            toRender = <LoginPanel loginUser={this.handleLoginClick} handleInputChange={this.handleLoginInputChange} />
        }

        if (obj.status === "pending") {
            toRender = <CreateAccountPanel />
        }

        if (obj.status === "pending" && obj.id) {
            toRender = <CreateProfilePanel />
        }

        if (obj.status === "pending" && obj.id && obj.profile) {
            toRender = <SearchPanel />
        }

        if(obj.status === "complete"){
            toRender = <MatchesPage />
        }
        return toRender;
    }



    render(){
        return(
            <div>
                {this.componentToRender(this.props.userIsLoggedIn.success, this.props.accountWasCompleted)}
            </div>
        )
    }
}


export default connect(mapStateToProps, matchDispatchtoProps)(App);


